import { Component, OnInit } from '@angular/core';
import { RecorderStore } from '../../../stores/recorder.store';
import { forceDataDownload } from '../../../../shared/helpers/force-download';

@Component({
  selector: 'app-share-download',
  templateUrl: './share-download.component.html',
  styleUrls: ['./share-download.component.styl']
})
export class ShareDownloadComponent implements OnInit {
  processing = false;

  constructor(private recorderStore: RecorderStore) {
  }

  ngOnInit() {
  }

  onButtonClick() {
    if (this.processing === true) {
      return;
    }

    this.processing = true;

    this.recorderStore.getCompiledRecord()
      .then(mp3Data => {
        this.processing = false;

        forceDataDownload(mp3Data, 'audio/mp3', 'dictaphone.audio.mp3');
      });
  }
}



