import { Pipe, PipeTransform } from '@angular/core';
import { Hours, Milliseconds, Minutes, Seconds } from '../../types';

const millisecondsInSecond = 1000;
const millisecondsInMinute = 60 * millisecondsInSecond;
const millisecondsInHour = 60 * millisecondsInMinute;

@Pipe({
  name: 'milliseconds'
})
export class MillisecondsPipe implements PipeTransform {
  transform(milliseconds: Milliseconds): string {
    const hours = getHours(milliseconds);

    milliseconds -= hours * millisecondsInHour;

    const minutes = getMinutes(milliseconds);

    milliseconds -= minutes * millisecondsInMinute;

    const seconds = getSeconds(milliseconds);

    milliseconds -= seconds * millisecondsInSecond;

    return padNumber(hours, 2) + ':' +
      padNumber(minutes, 2) + ':' +
      padNumber(seconds, 2) + '.' +
      padNumber(milliseconds, 3);
  }
}

function padNumber(number: number, length: number): string {
  let string: string = number.toString();

  while (string.length < length) {
    string = '0' + string;
  }

  return string;
}

function getHours(milliseconds: Milliseconds): Hours {
  return Math.floor(milliseconds / millisecondsInHour);
}

function getMinutes(milliseconds: Milliseconds): Minutes {
  return Math.floor(milliseconds / millisecondsInMinute);
}

function getSeconds(milliseconds: Milliseconds): Seconds {
  return Math.floor(milliseconds / millisecondsInSecond);
}



