import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { TimelineStore } from '../../timeline.store';
import { Subscriber } from 'rxjs/Subscriber';
import { Milliseconds, Pixels, ZoomValue } from '../../../../types';

@Component({
  selector: 'app-timeline-scale-tick',
  templateUrl: './timeline-scale-tick.component.html',
  styleUrls: ['./timeline-scale-tick.component.styl']
})
export class TimelineScaleTickComponent implements OnInit, OnDestroy {
  @Input() position: Milliseconds;

  public positionInPixels: Pixels = 0;

  public zoomChangeSubscriber: Subscriber<ZoomValue>;

  constructor(public timelineStore: TimelineStore) {
  }

  ngOnInit() {
    this.updatePositionInPixels();

    this.zoomChangeSubscriber = this.timelineStore.zoomChange
      .subscribe(() => this.updatePositionInPixels());
  }

  ngOnDestroy() {
    this.zoomChangeSubscriber.unsubscribe();
  }

  updatePositionInPixels() {
    this.positionInPixels = this.getPositionInPixels();
  }

  getPositionInPixels() {
    return this.timelineStore.convertMillisecondsToPixels(this.position);
  }
}



