import { Component, OnInit, OnDestroy } from '@angular/core';
import { TimelineStore } from '../timeline.store';
import { Subscriber } from 'rxjs/Subscriber';
import { TIMELINE_CANVAS_WIDTH } from '../../../constants';
import { LayerId, Milliseconds } from '../../../types';
import { getIntermediateValues } from '../../../shared/helpers/get-intermediate-values';
import { lowerDividend } from '../../../shared/helpers/math.helpers';
import { RecorderStore } from '../../stores/recorder.store';

@Component({
  selector: 'app-timeline-layers',
  templateUrl: './timeline-layers.component.html',
  styleUrls: ['./timeline-layers.component.styl']
})
export class TimelineLayersComponent implements OnInit, OnDestroy {
  public canvasPositions: Milliseconds[] = [];

  private timelineChangeSubscriber: Subscriber<number>;

  private previousStartValue: Milliseconds;
  private previousEndValue: Milliseconds;
  private previousStep: Milliseconds;

  constructor(
    private timelineStore: TimelineStore,
    private recorderStore: RecorderStore,
  ) {
  }

  get layerIds(): LayerId[] {
    return this.recorderStore.recordData.layerIds;
  }

  ngOnInit() {
    this.updateCanvasPositions();

    this.timelineChangeSubscriber = this.timelineStore.change
      .subscribe(() => this.updateCanvasPositions());
  }

  ngOnDestroy() {
    this.timelineChangeSubscriber.unsubscribe();
  }

  private updateCanvasPositions() {
    this.canvasPositions = this.getCanvasPositions();
  }

  private getCanvasPositions() {
    const step = this.timelineStore.convertPixelsToMilliseconds(TIMELINE_CANVAS_WIDTH);
    const startValue = lowerDividend(this.timelineStore.startVisiblePosition, step);
    const endValue = lowerDividend(this.timelineStore.endVisiblePosition, step);

    if (this.shouldPositionsUpdate(startValue, endValue, step)) {
      this.previousStep = step;
      this.previousStartValue = startValue;
      this.previousEndValue = endValue;

      return getIntermediateValues(startValue, endValue, step);
    } else {
      return this.canvasPositions;
    }
  }

  private shouldPositionsUpdate(startValue: Milliseconds, endValue: Milliseconds, step: Milliseconds): boolean {
    return startValue !== this.previousStartValue ||
      endValue !== this.previousEndValue ||
      step !== this.previousStep;
  }
}



